import React from 'react';
import './flip.css';
import clogo from './clogo.png';

function App() {
  return (
    <div className="container mt-5">
      <div className="row">
        <div className="col-md-6 mx-auto">
          <input type="checkbox" id="cover-checkbox" />
          <input type="checkbox" id="page1-checkbox" />
          <input type="checkbox" id="page2-checkbox" />
          <input type="checkbox" id="page3-checkbox" />

          <div className="book">
            <div className="cover">
              <label htmlFor="cover-checkbox"></label>
            </div>
            <div className="page" id="page1">
              <div className="front-page p-3">
                <p>
                  Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nihil
                  magni laudantium beatae quia. Recusandae, fuga quas consectetur
                  perferendis aperiam esse velit veniam ducimus? Quisquam consequatur
                  perferendis quidem quia, recusandae ab!
                </p>
                <label className="next" htmlFor="page1-checkbox">
                  <i className="fas fa-chevron-right"></i>
                </label>
              </div>
              <div className="back-page p-3">
                <img src={clogo} alt="Page 1" className="img-fluid" />
                <label className="prev" htmlFor="page1-checkbox">
                  <i className="fas fa-chevron-left"></i>
                </label>
              </div>
            </div>
            <div className="page" id="page2">
              <div className="front-page p-3">
                <h2>Page 2</h2>
                <p>
                  Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nihil
                  magni laudantium beatae quia. Recusandae, fuga quas consectetur
                  perferendis aperiam esse velit veniam ducimus? Quisquam consequatur
                  perferendis quidem quia, recusandae ab!
                </p>
                <label className="next" htmlFor="page2-checkbox">
                  <i className="fas fa-chevron-right"></i>
                </label>
              </div>
              <div className="back-page p-3">
                <img src={clogo} alt="Page 2" className="img-fluid" />
                <label className="prev" htmlFor="page2-checkbox">
                  <i className="fas fa-chevron-left"></i>
                </label>
              </div>
            </div>
            <div className="page" id="page3">
              <div className="front-page p-3">
                <h2>Page 3</h2>
                <p>
                  Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nihil
                  magni laudantium beatae quia. Recusandae, fuga quas consectetur
                  perferendis aperiam esse velit veniam ducimus? Quisquam consequatur
                  perferendis quidem quia, recusandae ab!
                </p>
              </div>
            </div>
            <div className="back-cover"></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
